import React, { useRef, useEffect } from 'react';
import './Home.css';
import Header from '../components/Header';
import Footer from '../components/Footer';

import Introduction from './Introduction';
import Farm from './Farm';
import Train from './Train';
import Pillage from './Pillage';
import Faq from './Faq';
import RoadMap from './RoadMap';

import AppBar from '@mui/material/AppBar';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import HomeIcon from '@mui/icons-material/Home';
import CircleIcon from '@mui/icons-material/Circle';


const Home = () => {
  const homeRef = useRef();
  const introductionRef = useRef();
  const trainRef = useRef();
  const farmRef = useRef();
  const pillageRef = useRef();
  const faqRef = useRef();
  const roadmapRef = useRef();

  useEffect(() => {
    if (window.location.hash) {
      scrollToSection(window.location.hash);
    }
  }, []);

  const pageSections = [
    { text: 'Home', id: 'home', icon: HomeIcon },
    { text: 'Introduction', id: 'introduction', icon: CircleIcon },
    { text: 'Train', id: 'train', icon: CircleIcon },
    { text: 'Farm', id: 'farm', icon: CircleIcon },
    { text: 'Pillage', id: 'pillage', icon: CircleIcon },
    { text: 'Faq', id: 'faq', icon: CircleIcon },
    { text: 'Roadmap', id: 'roadmap', icon: CircleIcon },
  ];

  function scrollToSection(pageSection) {
    let ref;
    switch (pageSection) {
      case '#introduction':
        ref = introductionRef;
        break;
      case '#train':
        ref = trainRef;
        break;
      case '#farm':
        ref = farmRef;
        break;
      case '#pillage':
        ref = pillageRef;
        break;
      case '#faq':
        ref = faqRef;
        break;
      case '#roadmap':
        ref = roadmapRef;
        break;
      default:
        ref = homeRef;
        break;
    }

    ref.current.scrollIntoView({ behavior: 'smooth' });
  }

  return (
    <>
      <main ref={homeRef}>
        <div className='home'>
          <div className='home-content'>
            <Header scrollTo={scrollToSection} pageSections={pageSections} />

            <Container maxWidth='md'>
              <Typography
                component='h1'
                variant='h2'
                align='center'
                color='text.primary'
                gutterBottom
                className='logo-text'
              >
                ETHERORCS
              </Typography>

              <Typography
                component='h4'
                variant='h4'
                align='center'
                color='text.primary'
                gutterBottom
                className='slogan'
              >
                TRAIN, FARM AND PILLAGE
              </Typography>

              <Typography
                className='banner-text desktop-only'
                variant='h6'
                align='center'
                color='text.secondary'
                paragraph
              >
                Battle your Orcs in the very first 100% on-chain NFT RPG.
                Pillage loot pools to upgrade your equipment and meta-data,
                another industry first. For the Horde!
              </Typography>

              {/* <Stack
              sx={{ pt: 4 }}
              direction="row"
              spacing={2}
              justifyContent="center"
            >
              <Button variant="contained">ENTER ORC TAVERN</Button>
            </Stack>
          */}
              {/* <Typography variant="h6" color="text.secondary" align="center" paragraph>
                Scroll down to learn more
                <br/>
                <ArrowCircleDownIcon/>
            </Typography> */}
            </Container>
          </div>
        </div>

        <Container
          sx={{ pb: 4 }}
          maxWidth='lg'
          className='banner-mobile-extended mobile-only'
        >
          <Typography
            className='banner-mobile-text'
            variant='h5'
            align='center'
            color='text.secondary'
            paragraph
          >
            Battle your Orcs in the very first 100% on-chain NFT RPG. Pillage
            loot pools to upgrade your equipment and meta-data, another industry
            first. For the Horde!
          </Typography>
        </Container>

        <div ref={introductionRef} style={{backgroundColor: "black"}} >
          <Introduction />
        </div>

        <div ref={trainRef} style={{backgroundColor: "#111"}}>
          <Train />
        </div>

        <div ref={farmRef} style={{backgroundColor: "#222"}}>
          <Farm />
        </div>

        <div ref={pillageRef} style={{backgroundColor: "#111"}}>
          <Pillage />
        </div>

        <div ref={faqRef} style={{backgroundColor: "#222"}}>
          <Faq />
        </div>

        <div ref={roadmapRef} style={{backgroundColor: "#111"}}>
          <RoadMap />
        </div>
      </main>

      <Footer />
    </>
  );
};

export default Home;
