import React, { useRef, useEffect } from 'react';
import './Home.css';
import Header from '../components/Header';
import AppBar from '@mui/material/AppBar';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import Paper from '@mui/material/Paper';

const RoadMap = () => {
  return (
    <Container sx={{ py: 8 }} maxWidth='lg' >
      <Typography
        component='h4'
        variant='h4'
        align='center'
        color='text.primary'
        gutterBottom
        className='section-heading'
      >
        ROADMAP
      </Typography>
      <Typography variant='p' align='left' paragraph>
        Unlike other projects, we delivered a working role-playing GAME THAT
        TAKES PLACE 100% ON-CHAIN WITH NO IPFS OR API before releasing our
        roadmap or website.
        <br />
        Everything outlined on this website including farming, pillaging and
        training through staking is fully operational and can be accessed via
        our <a href="https://etherorcstavern.jollyvain.xyz/" target="_blank">DAPP</a>.
        <br />
        With that said, here are some of our plans for the future of this
        project.
      </Typography>
      <Stack spacing={2}>
        <Paper sx={{ p: 4 }}>Bone Shards (confirmed)</Paper>
        <Typography sx={{ pl: 2, pr: 2 }} variant="p" align="left" paragraph>
        Bone shards are a utility token that will resurrect new warriors to aid Genesis Orcs in their
conquest of the game world. Those new warriors will be in their own NFT collection distinct
from Genesis Orcs. Bone shards will be obtainable by completing RAIDs and will also be found in
Dungeon Crawls / future phases of EtherOrcs. Your local necromancer can help you with this
process once you have acquired enough shards.
As previously stated, ONLY Genesis Orcs will ever be able to create ZUG. However, these new
warriors will have unique skills and abilities that Genesis Orcs will see great value in – whether
recruiting them to join their RAID or trading them ZUG for supplies. All future player character
NFT collections within the EtherOrcs game system will be generated using bone shards, and
each character will have a unique in-game utility all their own.

        </Typography>

        <Paper sx={{ p: 4 }}>Raids (confirmed)</Paper>
        <Typography sx={{ pl: 2, pr: 2 }} variant="p" align="left" paragraph>
        Orcs can team up with a party of fellow Orcs to tackle RAID zones. RAIDs will take a party of
        Orcs several days to complete. In order to RAID, Orcs will be required to meet a level threshold
        for a given RAID zone. Additionally, RAIDs will cost significant ZUG to embark upon per Orc. If
        you are a lonewolf, there will be an option to take your Orc on solo RAIDs as well.
        When completed, RAIDs will yield bone shards as a reward for all party members (party
        members always receive equivalent rewards). Additionally, there is a chance your party may
        gain better rewards such as consumables, rare items for future utilization, and additional bone
        shards.
        <br/>
That probability will be enhanced by a number of factors – relative importance of factor is noted
and will be refined so that there is strong incentive for ALL ORCs to be included in parties:
<br/>
● Level (limited to a max bonus based on a RAIDs minimum level requirement) – HIGH<br/>
● Party composition (e.g. combination of high gear and low gear orcs, magic users + melee users) – MEDIUM<br/>
● Zone specific bonuses (e.g. fire weapons in a flammable environment, bombs in a cavern) – MEDIUM<br/>
● Equipment bonuses (e.g. equipment tier bonus) – LOW<br/>
● Set bonuses (e.g. Helix set) – LOW<br/><br/>

While RAID bonuses will help tilt certain parties to better outcomes, a party with low RAID
bonus will still feel they have a chance for a better reward and are still guaranteed at least one
bone shard as a reward. The “optimization” of a party should be optional for players who want
to invest the additional time.<br/>

Finally, a party may purchase tradeable consumables from the Potion Vendor. The Potion
Vendor will always be ready to sell basic potions and items for ZUG that will enhance your party.
The most simple example is the Healing Potion – reduces the time needed to complete the RAID
for the total party. Future player character NFTs generated by bone shards may be able to
produce consumables of their own as well…<br/>

Knowing there is significant new complexity in the RAID system – we will be working on ways to
make it easy to find parties and get your party’s RAID bonus value.
While future player character NFTs generated by bone shards will be able to participate in
RAIDs, ALL RAIDs must be led by at least one Genesis Orc.
These basic dynamics launched by RAIDs will setup the core structure for the entire EtherOrcs
game system.<br/>

As always, all of our phases will be focused on three areas:<br/>
● Creating long term value for Genesis Orcs<br/>
● Creating new utilization for ZUG<br/>
● Establishing meaningful points of entry for new users
        </Typography>
        <Paper sx={{ p: 4 }}>Dungeon Crawlers (Planned)</Paper>
        <Typography sx={{ pl: 2, pr: 2 }} variant="p" align="left" paragraph>
          More details coming soon
        </Typography>
      </Stack>
    </Container>
  );
}
export default RoadMap