import React, { useRef, useEffect } from 'react';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';
import { styled } from '@mui/material/styles';

const Introduction = () => {
  return (
    <Container sx={{ py: 8 }} maxWidth='lg'>

      <div className="ribbon-text">
      <Typography
        component='h4'
        variant='h4'
        align='center'
        color='text.primary'
        gutterBottom
        className='section-heading'
      >
        INTRODUCTION
      </Typography>
      </div>

      <Typography variant='p' align='left' paragraph>
        EtherOrcs is a collection of 5050 Orcs ready to pillage the blockchain.
        With no IPFS or API, these Orcs are the very first role-playing game
        that takes place 100% on-chain. Spawn new Orcs, battle your Orc to level
        up, and pillage different loot pools to get new weapons and gear which
        upgrades your Orc metadata. This Horde of Orcs will stand the test of
        time and live on the blockchain for eternity.
      </Typography>

      <Grid align='center' container spacing={2} sx={{ pb: 4 }}>
        <Grid item md={3} xs={6} align='center'>
          <img
            width='100%'
            src={require('./../assets/images/orc-green.svg').default}
            alt='Orcs 1'
          />
        </Grid>
        <Grid item md={3} xs={6}  align='center'>
          <img
            width='100%'
            src={require('./../assets/images/orc-green2.svg').default}
            alt='Orcs 2'
          />
        </Grid>
        <Grid  item md={3} xs={6}  align='center'>
          <img
            width='100%'
            src={require('./../assets/images/orc-green3.svg').default}
            alt='Orcs 3'
          />
        </Grid>
        <Grid  item md={3} xs={6}  align='center'>
          <img
            width='100%'
            src={require('./../assets/images/orc-green4.svg').default}
            alt='Orcs 3'
          />
        </Grid>
      </Grid>

        <Typography variant='p' align='left' paragraph>
          Orcs start with four attributes:
        </Typography>
        <ul>
          <li>Level (same for all orcs): 0</li>
          <li>Orcs have the same base farming rate of 4 $ZUG/day when staked</li>
          <li>Orcs have the same base “leveling” rate when staked for training</li>
          <li>3 Equipment Slots (helm, mainhand, offhand): Random starter equipment with small chance for equipment with minor bonus to ZUG generation</li>
          <li>Color: Aesthetic differences, but no impact on utility</li>
       </ul>
       
       <Typography variant='p' align='left' paragraph>
       Orcs can have one of four states and cannot do more than one of these at a time:
        </Typography>
        <ul>
          <li>Training (staked)</li>
          <li>Pillaging (contract interaction)</li>
          <li>Farming Zug (staked)</li>
          <li>Doing nothing (Idle)</li>
       </ul>
      

      {/* End hero unit */}
      <Grid container spacing={4}>
        <Grid item xs={12} sm={6} md={4}>
          <Card
            sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
          >
            <CardMedia
              component='img'
              image={require('./../assets/images/train.png').default}
              alt='random'
            />
            <CardContent sx={{ flexGrow: 1 }}>
              <Typography gutterBottom variant='h5' component='h2'>
                TRAIN
              </Typography>
              <Typography>
                Train your orcs to gain experience and level up. Higher level
                orcs have access to higher tier loot pools to pillage for.
              </Typography>
            </CardContent>
            {/* <CardActions>
              <Button size='small'>LEARN MORE</Button>
            </CardActions> */}
          </Card>
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Card
            sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
          >
            <CardMedia
              component='img'
              image={require('./../assets/images/farm.png').default}
              alt='random'
            />
            <CardContent sx={{ flexGrow: 1 }}>
              <Typography gutterBottom variant='h5' component='h2'>
                FARM
              </Typography>
              <Typography>
                Send your orcs out farming to earn our ERC20 utility token $ZUG.
                The better your items the more $ZUG you earn every day.
              </Typography>
            </CardContent>
            {/* <CardActions>
              <Button size='small'>LEARN MORE</Button>
            </CardActions> */}
          </Card>
        </Grid>
        <Grid item xs={12} sm={6} md={4}>
          <Card
            sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
          >
            <CardMedia
              component='img'
              image={require('./../assets/images/pillage.png').default}
              alt='random'
            />
            <CardContent sx={{ flexGrow: 1 }}>
              <Typography gutterBottom variant='h5' component='h2'>
                PILLAGE
              </Typography>
              <Typography>
                Pillage with your orcs in one of our many loot pools to upgrade
                their gear. Better gear increases daily $ZUG production when
                farming.
              </Typography>
            </CardContent>
            {/* <CardActions>
              <Button size='small'>LEARN MORE</Button>
            </CardActions> */}
          </Card>
        </Grid>
      </Grid>
    </Container>
  );
};
export default Introduction;
