import React, { useRef, useEffect } from 'react';
import AppBar from '@mui/material/AppBar';
import Button from '@mui/material/Button';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Grid from '@mui/material/Grid';
import Stack from '@mui/material/Stack';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import ArrowCircleDownIcon from '@mui/icons-material/ArrowCircleDown';
import Paper from '@mui/material/Paper';

const Pillage = () => {

  const pillaging = [
    {text:'Town', description: "Orc must be Level 1+ to Pillage", img: "town.png"},
    {text:'Dungeon', description: "Orc must be Level 3+ to Pillage", img: "dungeon.png"},
    {text:'Crypt', description: "Orc must be Level 6+ to Pillage", img: "crypt.png"},
    {text:'Castle', description: "Orc must be Level 15+ to Pillage", img: "castle.png"},
    {text:'Dragons Lair', description: "Orc must be Level 25+ to Pillage", img: "dragons-lair.png"},
    {text:'The Ether', description: "Orc must be Level 36+ to Pillage", img: "the-ether.png"},
    {text:'Tainted Kingdom', description: "Orc must be Level 15+ to Pillage", img: "tainted-kingdom.jpg"},
    {text:'Oozing Den', description: "Orc must be Level 25+ to Pillage", img: "oozing-den.jpg"},
    {text:'Ancient Chamber', description: "Orc must be Level 45+ to Pillage", img: "ancient-chamber.png"},
    {text:'Orc Gods', description: "Orc must be Level 52+ to Pillage", img: "orc-gods.png"}
  ];

  return (
    <Container sx={{ py: 8 }} maxWidth="lg">
    <Typography
      component="h4"
      variant="h4"
      align="center"
      color="text.primary"
      gutterBottom
      className="section-heading"
    >
      PILLAGE
    </Typography>
  <Typography variant="p" align="left" paragraph>
  Pillaging is where you stake your Orc to a specific loot pool and hunt for rare items. 
  <br/>
  Any items pillaged in a loot pool is automatically added to your Orcs metadata and visible on your Orc NFT.
  </Typography>
  <Typography variant="p" align="left" paragraph>
  There are many loot pools that you can pillage and new ones are constantly being introduced. 
  Each one of these pools will have different level
  requirements to pillage from. 
  </Typography>
  <Typography variant="p" align="left" paragraph>
  Note that there is a 10 minute cool down after you loot. 
  You will not earn rewards from training/farming during that time and will have to wait 10 minutes to loot again. You do
  not have to unstake your Orc to pillage. After the 10 minutes are up, your Orc will return to whatever
  activity it was doing before you looted.
  </Typography>
  <Typography variant="p" align="left" paragraph>
  You can choose to pillage for one, two, or three of your item slots (it’s your choice which item slot(s) to
  pillage for). It’s important to note that while you can pillage as many times as you’d like, when an item
  gets replaced on your Orc, the original item is destroyed.
  </Typography>
  <Typography variant="p" align="left" paragraph>
  Each loot pool has 3 tiers of items. The lowest rarity items in a pool will increase your ZUG generation
  rate by that loot pool number. IE, if you loot the dungeon, the common items will increase your ZUG
  generation rate by 2 (the town 1, the crypt 3, castle 4, etc). Rarer items will add an additional +1 or even
  a +2 to your ZUG generation rate.
  </Typography>

    <Grid container spacing={4}>
    {pillaging.map(function(obj, index){
        return (
          <Grid item key={`pillage-${index}`} xs={12} sm={6} md={3}>
          <Card
            sx={{ height: '100%', display: 'flex', flexDirection: 'column' }}
          >
            <CardMedia
              component="img"
         
              image={require(`./../assets/images/dungeons/${obj.img}`).default}
              alt="pillage"
            />
            <CardContent sx={{ flexGrow: 1 }}>
              <Typography gutterBottom variant="h5" component="h2">
              {obj.text}
              </Typography>
              <Typography>
              {obj.description}
              </Typography>
            </CardContent>
            {/* <CardActions>
              <Button size="small">LEARN MORE</Button>
            </CardActions> */}
          </Card>
        </Grid>
        );
      })}
  </Grid>

  
</Container>
  );
}
export default Pillage