import React, { useRef, useEffect } from 'react';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';

const Train = () => {
  return (
    <Container sx={{ py: 8 }} maxWidth="lg">
    <Typography
      component="h4"
      variant="h4"
      align="center"
      color="text.primary"
      gutterBottom
      className="section-heading"
    >
      Train
    </Typography>
  <Typography variant="p" align="left" paragraph>
  Training is where you stake your Orc to increase your Orcs level. While training, your Orc will gain 1 level
  every 8 hours. The higher your level, the higher the loot pool you will have access to. Note that you can
  only pick one staked action to perform. IE, if you are training then you cannot also farm ZUG with that
  Orc.
  </Typography>
</Container>
  );
}
export default Train