import React, { useRef, useEffect } from 'react';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import Link from '@mui/material/Link';
import Paper from '@mui/material/Paper';

const Farm = () => {
  return (
    <Container sx={{ py: 8 }} maxWidth="lg">
    <Typography
      component="h4"
      variant="h4"
      align="center"
      color="text.primary"
      gutterBottom
      className="section-heading"
    >
      FARM
    </Typography>
  <Typography variant="p" align="left" paragraph>
  Farming is where you stake your Orc to earn $ZUG; an erc-20 token. 
  <br/>
  Genesis Orcs without any items earn 4 $ZUG per day, by pillaging for new gear, this base value can be increased up to 24 additional $ZUG per day.
  <br/>
  $ZUG is currently only used to access special loot pools when pillaging, but will have much more use cases in the near future.
  <br/>
  $ZUG was initially used to mint new Orcs, but all 5050 orcs have now been minted.<br/>
  Now YOU CAN only BUY ETHERORCS ON <a href="https://opensea.io/collection/ether-orcs" target="_blank">Open Sea</a> OR JOIN OUR <a href="https://discord.com/invite/etherorcs" target="_blank">discord</a> AND CHECK THE #TRADING-POST CHANNEL FOR TRADES.
  
  </Typography>
</Container>
  );
}
export default Farm